import { CategoriaDTO } from './../../../../../models/domain/categoria.dto';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { API_CONFIG } from 'config/core/api.config';
import { ProdutoService } from '../produto.service';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { ProdutoDTO } from 'models/domain/produto.dto';
import { TrataErrorService } from 'services/trata-error.service';
import { CategoriaService } from '../../../config/categoria/categoria.service';

@Component({
  templateUrl: './listar-produtos.component.html',
  styleUrls: ['./listar-produtos.component.scss']
})
export class ListarProdutosComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('modalExcluirProduto') popupExclusao;
  @ViewChild('modalChangeSatusProduto') popupChangeStatus;
  bucketUrl: string = API_CONFIG.bucketBaseUrl;
  produto: ProdutoDTO;

  items: ProdutoDTO[];
  categorias: CategoriaDTO[] = [];
  categoraisSelecionada: string[] = [];


  constructor(
    public produtoService: ProdutoService,
    public categoriaService: CategoriaService,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarDados();
  }

  public selecionarCheck(acao: boolean, item: ProdutoDTO) {
    if (acao) {
      this.categoraisSelecionada.push(item.id);
    } else {
      this.categoraisSelecionada = this.categoraisSelecionada.filter((cat: string) => cat != item.id);
    }
    this.carregarProdutos();
  }

  selecionarItem(item: ProdutoDTO) {
    this.produto = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: ProdutoDTO) {
    this.produto = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.produtoService.changeStatus(this.produto)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Produtos', 'Status do produto alterado com sucesso!!!')
        this.carregarProdutos();
      }, error => TrataErrorService.tratarError(error));
  }

  excluirCategoria() {
    this.produtoService.excluir(this.produto)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Produtos', 'Produto excluído com sucesso!!!')
        this.carregarProdutos();
      }, error => TrataErrorService.tratarError(error));
  }

  alterarProduto(produto: ProdutoDTO) {
    this.router.navigate([`produtos/editar/${produto.id}`])
  }

  private carregarDados() {
    this.categoriaService.findAll()
      .subscribe(
        res => {
          this.categorias = res;
          //for (let x of this.categorias) {
            //this.categoraisSelecionada.push(x.id);
          //}
          this.carregarProdutos();
        });
  }


  private carregarProdutos() {
    this.produtoService.findByCategorias(this.categoraisSelecionada)
      .subscribe(
        res => this.items = res,
        error => TrataErrorService.tratarError(error));

  };
}



