import { TrataErrorService } from './../../../../../services/trata-error.service';
import { Component, Input, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';


import { ProdutoComposicaoDTO } from 'models/domain/produto-composicao.dto';
import { ModalDirective } from 'ng2-bootstrap/modal/modal.component';
import { ComboDTO } from './../../../../../models/domain/combo.dto';
import { CategoriaService } from './../../../config/categoria/categoria.service';
import { ProdutoService } from './../produto.service';
import { EventEmitterService } from './../../../../shared/event/event-emitter.services';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { UnidadeMedidaService } from './../../../config/unidademedida/unidade-medida.service';


@Component({
  selector: 'composicao-produto-popup-modal',
  styleUrls: ['./composicao-produto-popup-modal.component.scss'],
  templateUrl: './composicao-produto-popup-modal.component.html'

})
export class ComposicaoProdutoPopupModalComponent extends AbstractPopupComponent implements OnInit {

  checkMostrar: boolean = true;
  comboCompUnidades: ComboDTO[] = []
  comboCompProdutos: ComboDTO[] = []
  formProdComp: FormGroup;

  constructor(
    public fb: FormBuilder,
    public unidadeService: UnidadeMedidaService,
    public produtoService: ProdutoService) {
    super('composicaoProdutoPopupModal');
  }

  ngOnInit() {

    this.criarFormProdComp();
    this.produtoService.findAllProdutoInterno()
      .subscribe(
        response => this.comboCompProdutos = this.preencharCombo(response, 'id', 'nmProduto'),
        error => TrataErrorService.tratarError(error)
      );
    this.unidadeService.findAllAtivo()
      .subscribe(
          response => this.comboCompUnidades = this.preencharCombo(response, 'id', 'sgUnidadeMedida'),
          error => TrataErrorService.tratarError(error)
      )
  }

  show() {
    this.criarFormProdComp();
    this.showPopup("Inclusão de produto na composição")
  }

  showAlteracao(item: ProdutoComposicaoDTO) {
    this.alterarProdutoComp(item)
    this.showPopup("Alteração de produto na composição");
  }

  btnConfirmar(value: any) {
    EventEmitterService.closePopup(this.nomeJanePopup);
    this.onClickConfirm.emit(value);
  }

  criarFormProdComp() {
    this.formProdComp = this.fb.group({
      idProdutoPai: [],
      produtoComp: ['', Validators.required],
      unidade: ['', Validators.required],
      qtUtilizada: ['', Validators.required],
      mostrarUnidade: ['S'],
      emAlteracao: [false]
    })
  }

  compProdCheckMostrar(valor: boolean) {
    this.formProdComp.patchValue({
      mostrarUnidade: valor ? 'S' : 'N'
    });
  }


  private alterarProdutoComp(item: ProdutoComposicaoDTO) {
    this.formProdComp.patchValue({
      produtoComp: [{ id: item.idProdutoComposicao, text: item.nmProdutoComposicao }],
      unidade: [{ id: item.idUnidadeMedida, text: item.nmUnidadeMedida }],
      qtUtilizada: item.qtUtilizada,
      mostrarUnidade: item.mostrarUnidade,
      emAlteracao: true
    });
  }

}



