import { TrataErrorService } from 'services/trata-error.service';
import { UnidadeMedidaService } from './../../config/unidademedida/unidade-medida.service';
import { ProdutoService } from './produto.service';
import { CategoriaService } from './../../config/categoria/categoria.service';
import { ProdutoInfoNutricionalDTO } from './../../../../models/domain/produto-info-nutricional.dto';
import { ProdutoDTO } from 'models/domain/produto.dto';
import { CategoriaDTO } from 'models/domain/categoria.dto';
import { ProdutoComposicaoDTO } from './../../../../models/domain/produto-composicao.dto';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';

import { ComboDTO } from 'models/domain/combo.dto';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { SelectComponent, SelectItem } from 'ng2-select';


export abstract class AbstractProdutoComponent extends AbstractBaseComponent {


  public prodComp: ComboDTO;
  public categorias: CategoriaDTO[];

  public produtosComposicoes: ProdutoComposicaoDTO[] = [];
  public produtosInfoNutricionais: ProdutoInfoNutricionalDTO[] = [];
  public comboUnidades: ComboDTO[] = [];
  public comboUnidadesEmCasa: ComboDTO[] = [];
  public comboCategorias: ComboDTO[] = [];

  constructor(
    public fb: FormBuilder,
    public location: Location,
    public categoriaService: CategoriaService,
    public unidadeService: UnidadeMedidaService) {
    super();
  }

  public carregarCombosProduto() {
    this.categoriaService.findAll()
      .subscribe(
        response => this.comboCategorias = this.preencharCombo(response, 'id', 'nmCategoria'),
        error => TrataErrorService.tratarError(error)
      );
    this.unidadeService.findAll()
      .subscribe(
        response => {
          this.comboUnidades = this.preencharCombo(response, 'id', 'sgUnidadeMedida');
          this.comboUnidadesEmCasa = this.preencharCombo(response, 'id', 'sgUnidadeMedida')
        },
        error => TrataErrorService.tratarError(error)
      );
  }

  public voltar() {
    this.location.back();
  }

  mudarImage(evento) {
    this.atualizarFoto(evento.foto);
  }

  private atualizarFoto(foto: any) {
    this.urlFoto = foto;
  }

  protected criarFormulario() {
    this.formulario = this.fb.group({
      id: [''],
      nmProduto: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(50)]],
      vlVenda: ['', Validators.required],
      vlCompra: [''],
      status: ['A'],
      urlImagem: [''],
      categoria: ['', Validators.required],
      unidadeVenda: [''],
      qtAcompanhamento: [''],
      qtMinimaEstoque: [''],
      nrCodigoBarra: [''],
      dsUrlBlog: [''],
      dsInfoComplementar: [''],
      emCada: [''],
      qtEmcada: [''],
      composicoes: [''],
      infoNutricionais: ['']
    })
    this.urlFoto = this.urlFotoAvatar;
  }


  protected atualizarForm(produto: ProdutoDTO) {
    this.formulario.patchValue({
      id: produto.id,
      nmProduto: produto.nmProduto,
      vlVenda: produto.vlVenda,
      vlCompra: produto.vlCompra,
      status: produto.status,
      urlImagem: produto.urlImagem,
      qtMinimaEstoque: produto.qtMinimaEstoque,
      qtAcompanhamento: produto.qtAcompanhamento,
      dsUrlBlog: produto.dsUrlBlog,
      qtEmcada: produto.qtEmcada,
      nrCodigoBarra: produto.nrCodigoBarra,
      dsInfoComplementar: produto.dsInfoComplementar
    })
    if (produto.categoria != null) {
      this.formulario.patchValue({
        categoria: [{ id: produto.categoria.id, text: produto.categoria.nmCategoria }],
      })
    }
    if (produto.unidadeVenda != null) {
      this.formulario.patchValue({
        unidadeVenda: [{ id: produto.unidadeVenda.id, text: produto.unidadeVenda.nmUnidadeMedida }],
      })
    }
    if (produto.emCada != null) {
      this.formulario.patchValue({
        emCada: [{ id: produto.emCada.id, text: produto.emCada.nmUnidadeMedida }],
      })
    }
    if (produto.composicoes != null) {
      this.produtosComposicoes = produto.composicoes
    }
    if (produto.infoNutricionais != null) {
      this.produtosInfoNutricionais = produto.infoNutricionais;
    }
  }

  confirmarCompProd(value: any) {
    let prod = {} as ProdutoComposicaoDTO;
    prod.idProduto = value.idProdutoPai;
    prod.idProdutoComposicao = value.produtoComp[0].id;
    prod.nmProdutoComposicao = value.produtoComp[0].text;
    prod.idUnidadeMedida = value.unidade[0].id;
    prod.nmUnidadeMedida = value.unidade[0].text;
    prod.qtUtilizada = Number(value.qtUtilizada)
    prod.mostrarUnidade = value.mostrarUnidade
    prod.emAlteracao = value.emAlteracao;

    let position = this.produtosComposicoes.findIndex(x => x.idProdutoComposicao == prod.idProdutoComposicao);
    if (position == -1) {
      this.produtosComposicoes.push(prod);
    } else {
      if (prod.emAlteracao) {
        this.produtosComposicoes[position] = prod;
      } else {
        this.produtosComposicoes[position].qtUtilizada += prod.qtUtilizada
      }
    }
  }

  excluirItem(item: ProdutoComposicaoDTO) {
    this.produtosComposicoes =
      this.produtosComposicoes
        .filter((prod: ProdutoComposicaoDTO) => prod.idProdutoComposicao != item.idProdutoComposicao)
  }

  confirmarInfoNutricional(value: any) {
    let prod = {} as ProdutoInfoNutricionalDTO;
    prod.idProduto = value.idProduto;
    prod.idInfoNutricional = value.infoNutricional[0].id;
    prod.dsInfoNutricional = value.infoNutricional[0].text;
    prod.idUnidadeMedida = value.unidade[0].id;
    prod.sgUnidadeMedida = value.unidade[0].text;
    prod.qtUtilizada = Number(value.qtUtilizada)

    let position = this.produtosInfoNutricionais.findIndex(x => x.idInfoNutricional == prod.idInfoNutricional);
    if (position == -1) {
      this.produtosInfoNutricionais.push(prod);
    } else {
      if (prod.emAlteracao) {
        this.produtosInfoNutricionais[position] = prod;
      } else {
        this.produtosInfoNutricionais[position].qtUtilizada += prod.qtUtilizada;
      }
    }
  }

  excluirItemInfoNutriciobnal(item: ProdutoInfoNutricionalDTO) {
    this.produtosInfoNutricionais =
      this.produtosInfoNutricionais
        .filter((prod: ProdutoInfoNutricionalDTO) => prod.idInfoNutricional != item.idInfoNutricional)
  }

  protected preencharProduto(value: any): ProdutoDTO {
    let prod = {} as ProdutoDTO;
    if (value != null && value.id != null) {
      prod.id = value.id;
    }
    prod.nmProduto = value.nmProduto;
    prod.vlVenda = value.vlVenda;
    prod.vlCompra = value.vlCompra;
    prod.qtAcompanhamento =value.qtAcompanhamento;
    prod.status = 'A';
    prod.urlImagem = this.urlFoto;
    if (value.categoria != null && value.categoria[0].id != null) {
      prod.categoria = { id: value.categoria[0].id, nmCategoria: value.categoria[0].text }
    }
    if (value.unidadeVenda != null && value.unidadeVenda != null && value.unidadeVenda[0].id != null) {
      prod.unidadeVenda = { id: value.unidadeVenda[0].id, nmUnidadeMedida: value.unidadeVenda[0].text };
    }
    prod.qtMinimaEstoque = value.qtMinimaEstoque;
    prod.nrCodigoBarra = value.nrCodigoBarra;
    prod.dsUrlBlog = value.dsUrlBlog;
    prod.dsInfoComplementar = value.dsInfoComplementar;
    if (value.emCada != '' && value.emCada != null && value.emCada[0].id != null) {
      prod.emCada = { id: value.emCada[0].id, nmUnidadeMedida: value.emCada[0].text };
      prod.qtEmcada = value.qtEmcada;
    }
    prod.composicoes = this.produtosComposicoes;
    prod.infoNutricionais = this.produtosInfoNutricionais;
    return prod;
  }
}



