import { TrataErrorService } from 'services/trata-error.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';


import { ProdutoDTO } from 'models/domain/produto.dto';
import { ProdutoService } from './../produto.service';
import { CategoriaService } from './../../../config/categoria/categoria.service';
import { AbstractProdutoComponent } from '../abstract-produto.component';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { UnidadeMedidaService } from '../../../config/unidademedida/unidade-medida.service';




@Component({
  templateUrl: './create-produto.component.html',
  styleUrls: ['./create-produto.component.scss']
})
export class CreateProdutoComponent extends AbstractProdutoComponent implements OnInit {



  constructor(
    public fb: FormBuilder,
    public location: Location,
    public categoriaService: CategoriaService,
    public unidadeService: UnidadeMedidaService,
    public produtoService: ProdutoService,
    public router: Router) {
    super(fb, location, categoriaService, unidadeService);
  }

  ngOnInit() {
    this.criarFormulario();
    this.carregarCombosProduto();
  }

  confirmar(value: any) {
    let prod = this.preencharProduto(value);
    this.produtoService.salvar(prod)
      .subscribe(
        response => {
          EventEmitterService.emitirMensagemToasterSucesso('Produtos', 'Produto incluído com sucesso!!!');
          this.router.navigate(['/produtos/listar']);
        }, error => TrataErrorService.tratarError(error));
  }

}



