import { InfoNutricionalService } from './../../config/infonutricional/info-nutricional.service';
import { ComposicaoProdutoPopupModalComponent } from './popup/composicao-produto-popup-modal.component';
import { NgModule, LOCALE_ID } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { SelectModule } from 'ng2-select';
import { LaddaModule } from 'angular2-ladda';
import { ModalModule } from 'ng2-bootstrap/modal';
import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { ProdutoService } from './produto.service';
import { CategoriaService } from './../../config/categoria/categoria.service';
import { SharedComponentModule } from './../../../shared/shared-component.module';
import { ProdutoRoutingModule } from './produto-routing.module';
import { ListarProdutosComponent } from './listar/listar-produtos.component';
import { CreateProdutoComponent } from './create/create-produto.component';
import { EditProdutosComponent } from './edit/edit-produtos.component';
import { StorageService } from './../../../../services/storage.service';
import { NumberOnlyDirective } from '../../../shared/directive/number-only.directive';
import { TextMaskModule } from 'angular2-text-mask';
import { CurrencyMaskModule } from "ng2-currency-mask";
import { UnidadeMedidaService } from './../../config/unidademedida/unidade-medida.service';

import { TabsModule } from 'ng2-bootstrap/tabs';
import { InfoNutricionalProdutoPopupModalComponent } from './popup/info-nutricional-produto-popup-modal.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    PopupModalModule,
    HttpClientModule,
    ProdutoRoutingModule,
    SelectModule,
    TabsModule,
    LaddaModule,
    TextMaskModule,
    CurrencyMaskModule
  ],
  declarations: [
    ListarProdutosComponent,
    CreateProdutoComponent,
    EditProdutosComponent,
    ComposicaoProdutoPopupModalComponent,
    InfoNutricionalProdutoPopupModalComponent
  ],
  providers:[
    ProdutoService,
    CategoriaService,
    UnidadeMedidaService,
    StorageService,
    InfoNutricionalService
  ]
})
export class ProdutoModule { }



